.slanted-bg {
  position: relative;
  margin-top: -30px;

  background-color: $color-bg--secondary;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    display: block;
    width: 100%;
    height: 100px;

    background-image: url("../img/bg-triangle-white.svg");
    background-repeat: no-repeat;
    background-position: 50% 0;
  }
}
